.poster{
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  flex-wrap: nowrap;
}

img{
  height:401px;
  width:45%;
  background-position: cover;
}
.sign-in{
  color:black;
}

.logo{
  cursor: pointer;
}

.logo:hover{
  color: rgb(79, 79, 225);
}

.signup{
  margin-top: 0;
}

.box{
  height:6rem;
  width:6rem;
  background-image:url('./images/sell-home.webp');
  background-repeat:no-repeat;
  background-size:cover;
  background-position: center
}
circle {
  background-color: #d83b01;
  border-radius: 50%;
  color: #fff;
  height: 2.5em;
  position: relative;
  width: 2.5em;

  border: 1px solid transparent;
}

.circleText {
  text-align: center;
  height: 50%;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
}

.hover {
  background-color: #0078d4;
}

  